:root {

  --border: red solid 1px;

  --bg-color: hsl(0, 0%, 05%);

  --primary-color: hsl(0, 0%, 14%);

  --secundary-color: hsl(0, 0%, 20%);

  --white: hsl(0, 0%, 94%);

  --cyan: hsl(180, 100%, 70%);

  --secondary-cyan: hsl(191, 100%, 40%);

  --shadow: #0003 0 0.5rem 0.5rem 0.2rem;

  --3d-shadow: #0003 0 0.5rem 0.5rem 0.2rem , #fff1 0 -0.2rem 1rem;

  --trybe-color: #1eb720;
}

* {
  color: var(--white);
  font-family: Varela Round , sans-serif;
  margin: 0;
  padding: 0;
  text-align: center;
  text-decoration: none;
}

body {
  background-color: var(--bg-color);
}

h2 {
  color: var(--cyan);
  font-size: 2rem;
}

b {
  color: var(--secondary-cyan);
}

strong {
  color: var(--trybe-color);
}

strong:hover {
  filter: brightness(1.4);
  transition: ease-in-out 600ms;
}

p {
  text-align: justify;
}

.App {
  align-items: stretch;
  display: flex;
  flex-direction: column;
  width: 100%;
}
